body {
  padding-top: 20px;
  background-color: rgba(192, 238, 227, 0.6);
}

.phone-images {
  background-color: white;
  width: 450px;
  height: 450px;
  overflow: hidden;
  position: relative;
  float: left;
}
.col-md-10 {
  padding-left: 0;
}
.phones {
  list-style: none;
  display: flex;
  flex-direction: column;
}

.thumb {
  float: left;
  margin: -0.5em 1em 1.5em 0;
  padding-bottom: 1em;
  height: 100px;
  width: 100px;
}

.phones__btn-buy-wrapper {
  align-items: center;
  float: right;
  height: 100%;
  display: flex;
  margin: 0 1em;
}

.thumbnail {
  padding: 16px 18px 4px;
  border-radius: 8px;
}
.thumbnail:first-child {
  margin-top: 18px;
}
.shopping-cart {
  display: flex;
  justify-content: center;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;

  box-shadow: 0 0 10px 0 #ff6;
  border-radius: 10px;
  padding-right: 12px;
  margin-top: 30px;
}
.shopping-cart p {
  text-align: center;
  font-weight: 500;
  font-size: 1.28em;
}
.shopping-cart__list-item {
  display: flex;
  justify-content: space-between;

  text-transform: capitalize;
  padding: 6px 10px;
  margin: 0 10px 10px;
  margin-left: -20px;

  border: 4px outset rgba(156, 241, 255, 0.6);
  border-radius: 10px;

  background-color: rgba(177, 237, 255, 0.69);
  list-style: none;

  font-style: oblique;
}
.shopping-cart__list-item button {
  background-color: transparent;
  border: 1px solid #aac;
  color: #888;
  border-radius: 50%;
}
.shopping-cart__list-item button:hover {
  color: #111;
  border-color: #335;
}

@media (min-width: 992px) {
  .col-md-2 {
    width: 25%;
  }
  .col-md-10 {
    width: 75%;
  }
}
/** Detail View **/

ul.phone-thumbs {
  margin: 0;
  list-style: none;
}

ul.phone-thumbs li {
  border: 1px solid black;
  border-radius: 6px;
  display: inline-block;
  margin: 1em;
  background-color: white;
}

ul.phone-thumbs img {
  height: 100px;
  width: 100px;
  padding: 1em;
}

ul.phone-thumbs img:hover {
  transform: scale(1.02);
  cursor: pointer;
}

ul.specs {
  clear: both;
  margin: 0;
  padding: 0;
  list-style: none;
}

ul.specs > li{
  display: inline-block;
  width: 200px;
  vertical-align: top;
}

ul.specs > li > span{
  font-weight: bold;
  font-size: 1.2em;
}

ul.specs dt {
  font-weight: bold;
}

h1 {
  border-bottom: 1px solid gray;
}


.viewer {
  display: flex;
}
@media (max-width: 770px) {
  .viewer {
    flex-direction: column;
  }
}

.viewer__button-wrapper {
  display: flex;
  justify-content: space-around;
}
.viewer__button {
  margin: 18px 0;
  padding: 6px 20px;
  border-radius: 6px;
  border: none;
  font-weight: 600;
  text-shadow: 0 0 4px #000;
  color: rgba(255, 255, 255, 0.95);
  background-color: #00a6c0;
  transition: .1s ease-in-out;
}
.viewer__button:hover {
  transform: scale(1.05);
  color: #fff;
}
.viewer__button:focus {
  outline: none;
}
.viewer__button--add {
  background-color: rgba(107, 215, 36, 0.76);
}
.gallery {
  position: relative;
  width: 400px;
  height: 400px;
  margin-right: 20px;
  border-radius: 20px;
  border: 3px outset #977;
  box-sizing: border-box;
}
.gallery--inner {
  position: relative;
  overflow: hidden;
  width: 394px;
  height: 394px;
  border-radius: 20px;
  background: #aba;
}
.gallery-img {
  background-color: white;
  width: 400px;
  height: 100%;
}

.gallery button {
  position: absolute;
  display: inline-block;
  width: 46px;
  height: 100%;
  font-size: 20px;
  border: none;
  background: rgba(190, 190, 190, 0.1);
  text-align: center;
  z-index: 3;
}
.gallery button:focus {
  outline: none;
}
.gallery .left {
  border-bottom-left-radius: 20px;
  border-top-left-radius: 20px;
}
.gallery .right {
  right: 0;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}
.gallery--table {
  width: auto;
  height: 400px;
  border-collapse: collapse;
  transition: 0.5s;
}
